<template>
  <div>
    <el-container>
      <el-aside style="width: 200px;min-height: 100vh ;background-color: #001529">
        <div style="height: 60px;color: white;display:flex; align-items: center;justify-content: center">
          求职者控制台
        </div>
        <el-menu :default-active=$route.path active-text-color="#fff" background-color="#001529" router
                 style="border: none;background-color: red" text-color="rgba(255,255,255,0.65)">
          <el-menu-item index="/home">
            <template slot="title">
              <i class="el-icon-house"></i>
              <span>系统首页</span>
            </template>
          </el-menu-item>
          <el-menu-item index="/recruit">招聘信息</el-menu-item>
          <el-menu-item index="/interview">面试通知</el-menu-item>

          <el-submenu>
            <template slot="title">
              <i class="el-icon-menu"></i>
              <span>用户信息管理</span>
            </template>
            <el-menu-item index="self">查看用户信息</el-menu-item>
            <el-menu-item index="resume">查看个人简历</el-menu-item>
          </el-submenu>
          <el-menu-item index="/">用户退出</el-menu-item>
        </el-menu>
      </el-aside>
      <el-container>
        <el-header></el-header>
        <el-main style="display: flex;align-items: center;justify-content: center">
          <el-form ref="form" :model="users" label-width="80px" style="width: 500px">
            <el-form-item label="姓名">
              <el-input v-model="users.name"></el-input>
            </el-form-item>
            <el-form-item label="年龄">
              <el-input v-model="users.age"></el-input>
            </el-form-item>
            <el-form-item label="性别">
              <el-input v-model="users.sex"></el-input>
            </el-form-item>
            <el-form-item label="城市">
              <el-input v-model="users.city"></el-input>
            </el-form-item>
            <el-form-item label="民族">
              <el-input v-model="users.nationality"></el-input>
            </el-form-item>

            <el-form-item label="生日">
              <el-col :span="11" style="width: 100%">
                <el-date-picker type="date" placeholder="选择日期" v-model="users.birthday"
                                style="width: 100%;">

                </el-date-picker>
              </el-col>
            </el-form-item>
            <el-form-item label="专业">
              <el-input v-model="users.profession"></el-input>
            </el-form-item>
            <el-form-item label="毕业时间">
              <el-col :span="11" style="width: 100%">
                <el-date-picker type="date" placeholder="选择日期" v-model="users.graduationDate"
                                style="width: 100%;">

                </el-date-picker>
              </el-col>
            </el-form-item>
            <el-form-item label="毕业学校">
              <el-input v-model="users.graduateSchool"></el-input>
            </el-form-item>
            <el-form-item label="电话号码">
              <el-input v-model="users.telephoneNumber"></el-input>
            </el-form-item>
            <el-form-item label="工作意向">
              <el-input v-model="users.jobIntention"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="save">保存</el-button>
              <el-button type="danger" @click="flushed">取消保存</el-button>
            </el-form-item>
          </el-form>
        </el-main>
      </el-container>

    </el-container>


  </div>
</template>
<style>
.el-menu--inline .el-menu-item {
  background-color: #000c17 !important;
}

.el-menu-item:hover, .el-submenu__title:hover {
  color: #fff !important;
}

.el-submenu__title:hover .el-icon-menu {
  color: #fff !important;
}

.el-menu-item.is-active {
  background-color: red !important;
}


</style>


<script>
import request from "@/utils/request";

export default {
  name: 'self',
  data() {
    return {
      users: {},

    }
  },
  mounted() {
    request.get('/user/self').then(res => {
      this.users = res.data.data
    })
  },
  methods: {
    save() {
      request.post('/user/update', this.users).then(res => {
            if (res.data.code === 1) {
              this.$message.success("保存成功")
              location.reload()
            } else {
              this.$message.error(res.data.message)
            }
          }
      )

    },flushed(){
      location.reload()
    }
  },
}
</script>
